import React from "react";
import { connect } from "react-redux";
import * as Actions from "../../store/JobDescription/JobDescriptionAction";
import { bindActionCreators } from "redux";
import TableComponent from "../../components/Controls/Table/sortableTable";
import Button from "../../components/Controls/Button/Button";
import SearchInput from "../../components/Atoms/CustomizedInputs/SearchInput";
import searchIcon from "../../assets/images/Icon feather-search.svg";
import { Chip } from "@material-ui/core";
import CalenderCard from "../../components/Controls/calenderCard/calenderCard";
import "./jd.css";
import DeleteIcon from "@material-ui/icons/Delete";
import CreateIcon from "@material-ui/icons/Create";
import PrintModal from "../../components/printModal";
import IconButton from "@material-ui/core/IconButton";
import SelectInput from "../../components/Atoms/SelectInput/SelectInput";
import CloseIcon from "@material-ui/icons/Close";
import DatePicker from "../../components/DatePicker/Datepicker";
import CustomizedInputs from "../../components/Atoms/CustomizedInputs/CustomizedInputs";
import { withRouter } from "react-router-dom";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import JoditEditor from "jodit-react";
import History from "../../routes/History";
import VisibilityIcon from "@material-ui/icons/Visibility";
import Title from "../../assets/images/Title.svg";
import Description from "../../assets/images/Description.svg";
import Skills from "../../assets/images/Skills.svg";
import ExperienceRequired from "../../assets/images/ExperienceRequired.svg";
import ActivePeriod from "../../assets/images/ActivePeriod.svg";
import Autocomplete from "../../components/Atoms/AutoComplete";
import * as JobDataActions from "../../store/JobData/JobDataAction";
import ReactGA from "react-ga";
import moment from "moment";
let optionsValue = [];
const config = {
  readonly: false,
  height: 300,
};

// let currentDate = new Date().toISOString();
let currentDate = new Date();
let date = new Date();
// date.setDate(date.getDate() - 30);
let updatedBackDate = new Date(
  date.getFullYear(),
  date.getMonth(),
  1
).toISOString();

class Jd extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      ssearchJd: false,
      data: [],
      optid: "",
      optname: "",
      status: "",
      value: "",
      goToPage: "",
      page: 1,
      limit: 10,
      startDate: updatedBackDate,
      endDate: currentDate,
      jdTitle: "",
      searchItem: "",
      fromEdit: false,
      isShowImportModal: false,
      editjdname: "",
      noOfHire: "",
      candidate_details: [],
      jobtitle: [],
      jdId: "",
      experienceFrom: 0,
      desc: "",
      experienceTo: 0,
      newStartDate: new Date(),
      newEndDate: new Date().setDate(new Date().getDate() + 7),
      // newStartDate: new Date().toLocaleDateString(),
      // newEndDate: new Date().toLocaleDateString(),
      ques_data: [],
      defaultValue: [],
      showModal: false,
      currentJD: "",
      isShowViewModal: false,
      multipleInputVal: [],
      editTitleId: null,
      options: [
        { value: "ONGOING", label: "ONGOING" },
        { value: "NOT_STARTED", label: "NOT_STARTED" },
        { value: "HOLD", label: "HOLD" },
        { value: "HIRED", label: "HIRED" },
      ],
      isDataArrived: true,
    };
  }

  componentDidMount() {
    ReactGA.pageview(window.location.pathname);
    const payload = {
      page: this.state.page,
      limit: this.state.limit,
      startDate: this.state.startDate,
      endDate: this.state.endDate,
    };
    this.props.JobDescriptionAction.getJobDescriptionData(payload);
    this.props.JobDataAction.getSkillS();
  }

  handleCloseModal = () => {
    this.setState({
      showModal: false,
    });
  };

  componentDidUpdate(prevProps) {
    const prev = prevProps && prevProps.JobDescriptionReducer;
    const cur = this.props && this.props.JobDescriptionReducer;
    if (prev.isJobDescriptionUpdated !== cur.isJobDescriptionUpdated) {
      this.setState({
        isShowImportModal: false,
      });
      const payload = {
        page: this.state.page,
        limit: this.state.limit,
        startDate: this.state.startDate,
        endDate: this.state.endDate,
      };
      this.props.JobDescriptionAction.getJobDescriptionData(payload);
    }
    if (prev.isJobDescriptionDeleted !== cur.isJobDescriptionDeleted) {
      this.setState({
        showModal: false,
      });
      const payload = {
        page: this.state.page,
        limit: this.state.limit,
        startDate: this.state.startDate,
        endDate: this.state.endDate,
      };
      this.props.JobDescriptionAction.getJobDescriptionData(payload);
    }
  }

  editClick = async (e) => {
    optionsValue = e.skills;
    await this.setState({
      skills: this.state.multipleInputVal
        ? [...optionsValue, this.state.multipleInputVal]
        : optionsValue,
    });
    await this.setState({
      job_id: e.id,
      multipleInputVal: e.skills,
      optname: e.status,
      editjdname: e.jdname,
      desc: e.desc,
      noOfHire: e.noOfHire,
      experienceFrom: e.experienceFrom,
      experienceTo: e.experienceTo,
      newStartDate: new Date(e.start),
      // .toString()
      // .replace(/\S+\s(\S+)\s(\d+)\s(\d+)\s.*/, "$2-$1-$3"),
      newEndDate: new Date(e.end),
      // .toString()
      // .replace(/\S+\s(\S+)\s(\d+)\s(\d+)\s.*/, "$2-$1-$3"),
      isShowImportModal: true,
      editTitleId: e.titleId,
    });
  };

  handleClose = (event, reason) => {
    this.setState({ isShowImportModal: false });
    if (reason === "clickaway") {
      return;
    }
  };
  handleCancel = (event, reason) => {
    this.setState({ isShowImportModal: false });
    if (reason === "clickaway") {
      return;
    }
  };
  handleSelectCand = (e) => {};

  handleSelectQues = (e) => {};

  setStartDate = async (date) => {
    this.setState(
      { startDate: date.selection.startDate, endDate: date.selection.endDate },
      () => {
        const payload = {
          page: this.state.page,
          limit: this.state.limit,
          title: this.state.searchItem,
          startDate: this.state.startDate,
          endDate: this.state.endDate,
        };
        this.props.JobDescriptionAction.getJobDescriptionData(payload);
      }
    );
  };

  // setEndDate = async (date) => {
  //   await this.setState({ endDate: date.toISOString() });
  //   if (this.state.startDate <= date.toISOString()) {
  //     const payload = {
  //       page: this.state.page,
  //       limit: this.state.limit,
  //       title:this.state.searchItem,
  //       startDate:this.state.startDate,
  //       endDate:this.state.endDate
  //     };
  //     this.props.JobDescriptionAction.getJobDescriptionData(payload);
  //   }
  // };

  handleSearch = (e) => {
    this.setState({ value: e.value, searchItem: e.target.value });
    if (e.target.value === "") {
      this.setState({
        searchJd: false,
      });
      const payload = {
        page: this.state.page,
        limit: this.state.limit,
        startDate: this.state.startDate,
        endDate: this.state.endDate,
      };
      this.props.JobDescriptionAction.getJobDescriptionData(payload);
    }
  };

  handleSearchJd = (e) => {
    const payload = {
      title: this.state.searchItem,
      page: this.state.page,
      limit: this.state.limit,
      startDate: this.state.startDate,
      endDate: this.state.endDate,
    };
    this.props.JobDescriptionAction.getJobDescriptionData(payload);
  };

  handleDeleteJD = () => {
    let id = this.state.currentJD && this.state.currentJD.id;
    this.props.JobDescriptionAction.deleteJobDescriptionData({
      id: id,
    });
  };

  static getDerivedStateFromProps(nextProps, currentState) {
    if (
      nextProps &&
      nextProps.JobDescriptionReducer &&
      nextProps.JobDescriptionReducer.getJobDescriptionData &&
      nextProps.JobDescriptionReducer.getJobDescriptionData.results &&
      (nextProps.JobDescriptionReducer.isGetJobDescriptionData ||
        nextProps.JobDescriptionReducer.isDateFilterJobDescriptionData)
    ) {
      nextProps.JobDescriptionReducer.isGetJobDescriptionData = false;
      nextProps.JobDescriptionReducer.isDateFilterJobDescriptionData = false;
      let data1 = [];
      let totalPage = 1;
      const jd = nextProps.JobDescriptionReducer;
      const newJd =
        jd && jd.getJobDescriptionData && jd.getJobDescriptionData.results;
      // nextProps.JobDescriptionReducer.getJobDescriptionData.data.code = 0;
      totalPage = 1;
      totalPage =
        nextProps.JobDescriptionReducer.getJobDescriptionData.totalPages;

      currentState.totalPage = totalPage;

      newJd &&
        newJd.map((item, index) => {
          const titleName = item.title;
          data1.push({
            no: index + 1,
            jdname:
              // " " +
              titleName,
            // +
            // " - " +
            // item.minExperience +
            // " to " +
            // item.maxExperience +
            // " Years of experience",
            // startdate: new Date(item.startDate)
            //   .toString()
            //   .replace(/\S+\s(\S+)\s(\d+)\s(\d+)\s.*/, "$2-$1-$3"),
            // enddate: new Date(item.endDate)
            //   .toString()
            //   .replace(/\S+\s(\S+)\s(\d+)\s(\d+)\s.*/, "$2-$1-$3"),
            startdate: moment(item.startDate).format("DD-MMM-YYYY"),
            enddate: moment(item.endDate).format("DD-MMM-YYYY"),
            end: item.endDate,
            start: item.startDate,
            status: item.status,
            id: item.id,
            desc: item.description,
            noOfHire: item.numberOfPositions,
            experienceFrom: item.minExp,
            experienceTo: item.maxExp,
            skills: item.skills,
            titleId: item && item.title && item.title._id ? item.title._id : "",
          });
        });

      currentState.data = data1;
      currentState.totalPage = totalPage;
    }

    return currentState;
  }

  handlePassId = (e) => {
    History.push({
      pathname: `/resumeSourcing`,
      state: {
        jdTitle: e.value,
        jdId: e.original.id,
      },
    });
  };

  handleBackPagination = async () => {
    if (this.state.page != 1) {
      let newPage = this.state.page - 1;
      await this.setState({ page: newPage });
      const payload = {
        page: newPage,
        limit: this.state.limit,
        title: this.state.searchItem,
        startDate: this.state.startDate,
        endDate: this.state.endDate,
      };
      this.props.JobDescriptionAction.getJobDescriptionData(payload);
    }
  };
  handleNextPagination = async () => {
    if (this.state.page != this.state.totalPage) {
      let newPage = this.state.page + 1;
      await this.setState({ page: newPage });
      const payload = {
        page: newPage,
        limit: this.state.limit,
        title: this.state.searchItem,
        startDate: this.state.startDate,
        endDate: this.state.endDate,
      };
      this.props.JobDescriptionAction.getJobDescriptionData(payload);
    }
  };

  handlePagination = async (e) => {
    await this.setState({ page: e });
  };
  handleUpdate = (event) => {
    const editorContent = event;
    var parser = new DOMParser();

    this.setState({
      desc: editorContent,
    });
  };
  handleNoOfHire = (e) => {
    this.setState({
      noOfHire: e.target.value,
    });
  };

  handleExperienceFrom = (e) => {
    this.setState({ experienceFrom: e.target.value });
  };

  handleExperienceTo = (e) => {
    this.setState({ experienceTo: e.target.value });
  };
  handleImportCancel = () => {
    this.setState({
      isShowImportModal: false,
      isShowfileDetails: false,
    });
  };
  handleJDSelect = (e) => {
    this.setState({ optname: e });
  };

  deleteClick = (e) => {};
  handleSave = (e) => {
    const {
      job_id,
      desc,
      noOfHire,
      newStartDate,
      newEndDate,
      experienceFrom,
      experienceTo,
      optname,
      editjdname,
    } = this.state;
    const payload = {
      id: job_id,
      description: desc,
      numberOfPositions: noOfHire,
      startDate: newStartDate,
      endDate: newEndDate,
      minExp: experienceFrom,
      maxExp: experienceTo,
      status: optname && optname.value ? optname.value : "",
      skills: optionsValue,
      title: editjdname,
    };

    this.props.JobDescriptionAction.updateJobDescriptionData(payload);
  };

  setLimit = (e) => {
    this.setState({
      limit: e,
      page: 1,
    });

    const payload = {
      page: this.state.page,
      limit: e,
      title: this.state.searchItem,
      startDate: this.state.startDate,
      endDate: this.state.endDate,
    };
    this.props.JobDescriptionAction.getJobDescriptionData(payload);
  };

  viewClick = (data) => {
    this.setState({
      isShowViewModal: true,
      currentJD: data,
    });
  };

  handleKeyPress = (event) => {
    if (event.key === "Enter") {
      this.setState({
        searchJd: false,
      });
      const payload = {
        page: this.state.page,
        limit: this.state.limit,
        title: this.state.searchItem,
        startDate: this.state.startDate,
        endDate: this.state.endDate,
      };
      this.props.JobDescriptionAction.getJobDescriptionData(payload);
    }
  };

  handleMutilpleSkills = (e) => {
    this.setState({
      multipleInputVal: e.target.value,
    });
  };
  renderValues = (value, getTagProps) => {
    optionsValue = value;
    let newVals = [];
    value.map((val) => {
      let i = val.split(",");
      i.length === 1 ? newVals.push(i[0]) : i.map((el) => newVals.push(el));
    });
    return newVals.map((option, index) => (
      <Chip variant='outlined' label={option} {...getTagProps({ index })} />
    ));
  };

  render() {
    let data1 = this.state.data;
    let totalPage = this.state.totalPage;
    const { JobDataReducer } = this.props;
    data1.sort((a, b) => {
      return new Date(b.startdate) - new Date(a.startdate);
    });

    let inboundDatacolumn = [
      {
        Header: "Job Description Name",
        accessor: "jdname",
        minWidth: 200,

        Cell: (e, val) => (
          <div style={{ display: "flex" }}>
            <div
              style={{
                // padding: "0 20px"
                padding: "0 9px",
              }}
            >
              {" "}
              {(this.state.page - 1) * this.state.limit + e.index + 1}.
            </div>
            <div
              style={{ color: "black", cursor: "pointer", color: "#0072E5" }}
              className='first-block'
              onClick={() => this.handlePassId(e)}
            >
              {e.value}
            </div>
          </div>
        ),
      },

      {
        Header: "Start Date",
        accessor: "startdate",
      },
      {
        Header: "End Date",
        accessor: "enddate",
      },
      {
        Header: "Status",
        accessor: "status",
      },
      {
        Header: "Actions",
        accessor: "actions",
        className: "header-action",
        Cell: (val) => {
          return (
            <div className='jd-button-user'>
              <div className='create'>
                <VisibilityIcon
                  onClick={(e) => this.viewClick(val.original)}
                  style={{ height: "14px", width: "14px", color: "#04724d" }}
                />
              </div>
              <div className='create'>
                <CreateIcon
                  onClick={(e) => this.editClick(val.original)}
                  style={{ height: "14px", width: "14px", color: "#0072E5" }}
                />
              </div>
              <div className='create'>
                <DeleteIcon
                  style={{ color: "red", height: "14px", width: "14px" }}
                  onClick={() => {
                    this.setState({
                      showModal: true,
                      currentJD: val.original,
                    });
                  }}
                />
              </div>
            </div>
          );
        },
      },
    ];

    let TitleImage = <img src={Title} />;
    let DescriptionImage = <img src={Description} />;
    let SkillsImage = <img src={Skills} />;
    let ExperienceRequiredImage = <img src={ExperienceRequired} />;
    let ActivePeriodImage = <img src={ActivePeriod} />;
    return (
      <div className='jd-wrap'>
        <PrintModal
          className='view-modal'
          isShowModal={this.state.isShowViewModal}
          onCloseModal={() => {
            this.setState({
              isShowViewModal: false,
            });
          }}
          // showClose={true}
        >
          <div className='job-description-heading'>Job Description Detail</div>

          <div className='job-description-body'>
            <div className='segment-wrapper'>
              <div className='modal-flex-div'>
                <div className='body-image-div'>{TitleImage}</div>
                <div className='body-title-div'>Job Title</div>
              </div>
              <div className='job-title-modal'>
                {this.state.currentJD && this.state.currentJD.jdname}{" "}
              </div>
            </div>

            <div className='segment-wrapper'>
              <div className='modal-flex-div'>
                <div className='body-image-div'>{DescriptionImage}</div>
                <div className='body-title-div'>Job Description</div>
              </div>
              <div className='job-title-modal'>
                {this.state.currentJD && this.state.currentJD.desc ? (
                  <div
                    dangerouslySetInnerHTML={{
                      __html: this.state.currentJD.desc,
                    }}
                  ></div>
                ) : null}{" "}
              </div>
            </div>

            <div className='segment-wrapper'>
              <div className='modal-flex-div'>
                <div className='body-image-div'>{SkillsImage}</div>
                <div className='body-title-div'>Skills</div>
              </div>
              {this.state.currentJD && this.state.currentJD.skills ? (
                <div className='job-skills-modal'>
                  {this.state.currentJD.skills.map((item) => {
                    return <div className='skill'> {item} </div>;
                  })}{" "}
                </div>
              ) : null}
            </div>

            <div className='flex-div-jd'>
              <div className='segment-wrapper'>
                <div className='modal-flex-div'>
                  <div className='body-image-div'>
                    {ExperienceRequiredImage}
                  </div>
                  <div className='body-title-div'>Experience Required</div>
                </div>
                <div className='job-title-modal'>
                  {this.state.currentJD && this.state.currentJD.experienceFrom}{" "}
                  to {this.state.currentJD && this.state.currentJD.experienceTo}{" "}
                  years
                </div>
              </div>

              <div className='segment-wrapper' style={{ marginLeft: "20px" }}>
                <div className='modal-flex-div'>
                  <div className='body-image-div'>{TitleImage}</div>
                  <div className='body-title-div'>No Of Hire</div>
                </div>
                <div className='job-title-modal'>
                  {this.state.currentJD && this.state.currentJD.noOfHire}{" "}
                </div>
              </div>
            </div>

            <div className='segment-wrapper' style={{ paddingBottom: "20px" }}>
              <div className='modal-flex-div'>
                <div className='body-image-div'>{ActivePeriodImage}</div>
                <div className='body-title-div'>Active Period</div>
              </div>
              <div style={{ display: "flex" }}>
                <div className='job-title-modal'>
                  Start date:
                  {this.state.currentJD && this.state.currentJD.startdate}{" "}
                </div>
                <div className='job-title-modal' style={{ marginLeft: "20px" }}>
                  End date:
                  {this.state.currentJD && this.state.currentJD.enddate}{" "}
                </div>
              </div>
            </div>
          </div>
        </PrintModal>

        <Dialog
          className='dialog-box'
          open={this.state.showModal}
          onClose={this.handleCloseModal}
          aria-labelledby='alert-dialog-title'
          aria-describedby='alert-dialog-description'
        >
          <DialogTitle
            id='alert-dialog-title'
            className='alert-dialog-title'
            style={{ fontSize: "11px" }}
          >
            {`Are You Sure you want to delete JD ${
              this.state.currentJD && this.state.currentJD.jdname
            }?`}
          </DialogTitle>

          <DialogActions>
            <div onClick={this.handleDeleteJD} className='Modal-div'>
              Yes
            </div>

            <div onClick={this.handleCloseModal} className='Modal-div'>
              Cancel
            </div>
          </DialogActions>
        </Dialog>
        <div className='main-wrapper'>
          <div className='jd-div-wrap-search'>
            <div className='addJD'>
              <div
                className='addJDbtn btnNewLine'
                onClick={() => {
                  this.props.history.push("/AddJD");
                }}
              >
                Add JD
              </div>

              <div className='searchInputWrap'>
                <SearchInput
                  className='jdSearch'
                  imageOnClick={() => {}}
                  placeholder='Search'
                  value={this.state.value}
                  onChange={(e) => {
                    this.handleSearch(e);
                  }}
                  imageClass='img-icon'
                  onKeyPress={(e) => this.handleKeyPress(e)}
                />
                <div className='searchiconJD'>
                  <img
                    src={searchIcon}
                    onClick={() => this.handleSearchJd(this.state.searchItem)}
                  />
                </div>
              </div>
            </div>
            <div style={{ width: "25%" }}>
              <CalenderCard
                handleSelectDate={(date) => this.setStartDate(date)}
                startDate={this.state.startDate}
                endDate={this.state.endDate}
              />
            </div>
          </div>

          <div className='table-left-space'>
            <TableComponent
              className={"jd-table"}
              data={data1}
              columns={inboundDatacolumn}
              sortable={true}
              showBottomPagination={true}
              pageSize={this.state.limit}
              setPageSize={(e) => this.setLimit(e)}
              gotoPage={(e) => this.handlePagination(e)}
              totalPage={this.state.totalPage}
              pageNumber={this.state.page ? this.state.page : 1}
              handlePrev={this.handleBackPagination}
              handleNext={this.handleNextPagination}
            />
          </div>
        </div>
        <PrintModal
          className='jd-modal'
          isShowModal={this.state.isShowImportModal}
          onCloseModal={this.handleImportCancel}
        >
          <IconButton
            className='closeIcon'
            size='small'
            aria-label='close'
            color='inherit'
            onClick={() => this.handleClose()}
          >
            <CloseIcon fontSize='small' />
          </IconButton>

          <div>
            <div className='hardware-modal px-4'>
              <div>
                <div style={{ fontSize: "14px", fontWeight: "bold" }}>
                  Update Job Description
                </div>
              </div>
              <hr />

              <div className='container'>
                <div className='row'>
                  <div className='col-12'>
                    <strong className='font-style'>JD Name</strong>
                    <div className='outer-div'>
                      <CustomizedInputs
                        placeholder='JD Name'
                        value={this.state.editjdname}
                        name='Username'
                        onChange={(e) => {
                          this.setState({
                            editjdname: e.target.value,
                          });
                        }}
                        // onKeyDown={this.editTitlehandler}
                      />
                    </div>
                  </div>
                  <div className='col-12  pt-4'>
                    <div className='addJdTextboxWrap'>
                      <strong className='font-style'>Description</strong>
                      <JoditEditor
                        ref={this.setStateeditor}
                        value={this.state.desc}
                        config={config}
                        onBlur={(newContent) => this.handleUpdate(newContent)}
                        onChange={(newContent) => this.handleUpdate(newContent)}
                      />
                    </div>
                  </div>
                  <div className='jd-skills'>
                    <strong className='font-style'>Skills</strong>
                    <div className='multiselectEmail'>
                      <Autocomplete
                        placeholder=''
                        multiple
                        className='multiSelectContainer skillsAutoJD'
                        onKeyDown={this.handleMutilpleSkills}
                        renderTags={(value, getTagProps) =>
                          this.renderValues(value, getTagProps)
                        }
                        defaultValue={optionsValue}
                        options={
                          (JobDataReducer &&
                            JobDataReducer.skillData &&
                            JobDataReducer.skillData.data &&
                            JobDataReducer.skillData.data.map((x, i) => {
                              return {
                                value: i,
                                label: x,
                              };
                            })) ||
                          []
                        }
                      />

                      {!optionsValue.length ? (
                        <div className='error'>Skills can't be empty</div>
                      ) : null}
                    </div>
                  </div>
                  <div className='col-12'>
                    <div
                      style={{
                        padding: "20px 0",
                        display: "flex",
                        marginTop: "20px",
                      }}
                    >
                      <div className='addJdDropdownWrapNew'>
                        <strong className='font-style'>
                          Select Jd Status *
                        </strong>
                        <div className='select-dropNew'>
                          <SelectInput
                            className='react-select jdPageModal'
                            onChange={this.handleJDSelect}
                            options={this.state.options}
                            placeholder={
                              this.state.optname
                                ? this.state.optname
                                : "Select Status *"
                            }
                            value={this.state.optname}
                          />
                        </div>
                      </div>

                      <div className='addJdHireWrapNew'>
                        <strong className='font-style'>No.of.Hire</strong>
                        <input
                          type='number'
                          name='noOfHire'
                          className='addJdHireInputNew'
                          onChange={this.handleNoOfHire}
                          placeholder='Enter No.of Hire'
                          value={this.state.noOfHire}
                        />
                      </div>
                    </div>

                    <div className='experienece'>
                      <div style={{ padding: "0 0 20px 0" }}>
                        <div>
                          <strong className='font-style'>
                            Experience Required *
                          </strong>
                          <div className='flex-div-new'>
                            <div className='addJdHireWrapNew div-1'>
                              <input
                                type='number'
                                name='experienceFrom'
                                className='addJdExpFromInput'
                                onChange={this.handleExperienceFrom}
                                placeholder='0'
                                value={this.state.experienceFrom}
                              />
                            </div>

                            <div className='div-2'> To </div>

                            <div className='addJdHireWrapNew div-3'>
                              <input
                                type='number'
                                name='experienceTo'
                                className='addJdExpToInput'
                                onChange={this.handleExperienceTo}
                                placeholder='0'
                                value={this.state.experienceTo}
                              />
                            </div>
                            {parseInt(this.state.experienceTo) <
                            parseInt(this.state.experienceFrom) ? (
                              <div
                                style={{
                                  color: "red",
                                  fontSize: "11px",
                                  marginLeft: "5px",
                                }}
                              >
                                {" "}
                                Experience To should be greater or equal to
                                Experience From
                              </div>
                            ) : null}
                          </div>

                          <div></div>

                          <div></div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div className='addJdDateWrap'>
                    <div className='col-4'>
                      <DatePicker
                        label='Start Date'
                        format='dd - MMM - yyyy'
                        name='datepicker'
                        value={this.state.newStartDate}
                        onChange={(date) =>
                          this.setState({ newStartDate: date })
                        }
                      />
                    </div>
                    {/* <p className="dateTo">To</p> */}

                    <div className='col-4'>
                      <DatePicker
                        label='End Date'
                        format='dd - MMM - yyyy'
                        name='datepicker'
                        value={this.state.newEndDate}
                        onChange={(date) => this.setState({ newEndDate: date })}
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div>
                <div className='button-div p-4'>
                  <Button className='cancel-button' onClick={this.handleCancel}>
                    Cancel
                  </Button>
                  <Button
                    className='cancel-button'
                    onClick={this.handleSave}
                    disabled={!optionsValue.length}
                  >
                    Update
                  </Button>
                </div>
              </div>
            </div>
          </div>
          <div></div>
        </PrintModal>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    JobDescriptionReducer: state.JobDescriptionReducer,
    JobDataReducer: state.JobDataReducer,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    JobDescriptionAction: bindActionCreators(Actions, dispatch),
    JobDataAction: bindActionCreators(JobDataActions, dispatch),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Jd));
